import React from 'react';
import {BrowserRouter, Switch, Route} from 'react-router-dom';

import Main from './main/main.jsx';

const App = () => {
  return (
    <BrowserRouter basename={process.env.PUBLIC_URL}>
      <Switch>
        <Route exact path="/">
          <Main />
        </Route>
      </Switch>
    </BrowserRouter>
  );
};

export default App;
